@import '../../constants/variable';

.co_privacy
{
    background-color: $bg-black;
    min-height: 100vh;

    nav
    {
        @include nav;
    }

    h1
    {
        font-weight: normal;
        font-size: 21px;
        color: #fff;
    }

    main
    {
        width: 60%;
        margin: 50px auto;
        margin-bottom: 0;

        @include rwd(phone)
        {
            width: 90%;
        }
    }

    p
    {
        margin: 0;
        font-size: 12px;

        &.time
        {
            color: $font-gray;
        }
    }
}